




import {Component, Prop, Vue} from 'vue-property-decorator'

@Component
export default class CSYoutubeVideo extends Vue {

  @Prop({required: true}) videoId!: string
  @Prop({required: false}) listId!: string
  @Prop({required: true}) width!: string
  @Prop({required: true}) height!: string

  //https://www.youtube.com/watch?v=pqFwQuD2zig&list=PL6iNjD8ENc-KnLYWMn60f8KiQxlu9bAQf
  // listType=playlist
  // &list=PLC77007E23FF423C6
  youtubeUrl(): string {
    let url = `https://www.youtube.com/embed/${this.videoId}?autoplay=0&controls=1&disablekb=0&
      modestbranding=1&playsinline=1&showinfo=0&enablejsapi=1&fs=1&iv_load_policy=3&
      loop=1&origin=${window.location.origin}&widgetid=1&frameborder=0&color=white`;
    if (this.listId) url += `&list=${this.listId}`
    return url
  }
}
