import colors from 'vuetify/es5/util/colors'
import model from '@/Model/Model'
import main from '@/main'

class Colors {

  get background(): string {
    return model.isDark ? '#232525' : '#FFFFFF'
  }

  get title(): string {
    return model.isDark ? '#FFFFFF' : '#000000'
  }

  get subtitle(): string {
    return this.secondary
  }

  get secondary(): string {
    return model.isDark ? '#BFBEBE' : '#484646'
  }

  get accent(): string {
    return model.isDark ? '#FFAB00' : '#0048ff'
  }

  get link(): string {
    return model.isDark ? '#f7ff00' : '#0048ff'
  }

  get button(): string {
    return '#0048ff'
  }

  get text(): string {
    return model.isDark ? '#FFFFFF' : '#000000'
  }

  get textOnDark(): string {
    return '#e7e7e7'
  }

  get textOnDarkSubtle(): string {
    return '#cccbcb'
  }

  get divider(): string {
    return model.isDark ? this.accent : '#868686'
  }

  get topBarMain(): string {
    return model.isDark ? 'rgba(66,66,66,0.97)' : 'rgba(255,255,255,0.95)'
  }

  get topBarChild(): string {
    return model.isDark ? 'rgba(27,27,27,0.95)' : 'rgba(255,255,255,0.95)'
  }

  get topBarAccent(): string {
    return model.isDark ? '#f7ff00' : '#0048ff'
  }

  get topBarHover(): string {
    return model.isDark ? 'rgba(247,255,0,1)' : 'rgb(0,35,134)'
  }

  get topBarDividerMain(): string {
    return model.isDark ? 'rgba(27,27,27,0)' : 'rgba(0,72,255,0.1)'
  }

  get topBarDividerChild(): string {
    return model.isDark ? 'rgba(27,27,27,0)' : 'rgba(0,72,255,0.1)'
  }

  get videoCloseButton(): string {
    return model.isDark ? colors.grey.lighten4 : colors.grey.darken3
  }

  get topBarClass(): string {
    return model.isDark ? 'grey darken-3' : 'grey lighten-4'
  }

  get topBarText(): string {
    return model.isDark ? colors.grey.lighten4 : colors.grey.darken4
  }

  get contentTextStyle(): string {
    return 'color:' + model.isDark ? colors.grey.lighten3 : colors.grey.darken3
  }

  get pageDescriptionTextClass(): string {
    return model.isDark ? 'grey--text text--lighten-4' : 'grey--text text--darken-3'
  }

  get menuClass(): string {
    return model.isDark ? 'grey darken-3' : 'grey lighten-2'
  }

  get menuItemTextClass(): string {
    return model.isDark ? 'grey--text text--lighten-4' : 'grey--text text--darken-3'
  }

  get footer(): string {
    return colors.grey.darken3
  }

  get footerText(): string {
    return model.isDark ? colors.grey.lighten4 : colors.grey.darken3
  }

  get footerBottom(): string {
    return this.footer
  }

  get footerTextStyle(): string {
    return 'color:' + this.footerText
  }

  get separator(): string {
    return colors.grey.darken4
  }

  updateTheme() {
    document.body.style.background = this.topBarMain
    main.$vuetify.theme.primary = this.link
    main.$vuetify.theme.secondary = this.secondary
    main.$vuetify.theme.accent = this.accent
  }
}

export default new Colors()
