import './assets/css/styles.styl'
import './class-component-hooks'
import './plugins/vuetify'
import '@/cs/CSExtensions.ts'
import router from './Router'
import {CSToast} from '@/cs/CSToast'
import {Component, Vue} from 'vue-property-decorator'
import {BaseVue} from '@/View/BaseVue'
import {later} from '@/cs/extensions/CSLang'
import ToastedPlugin from 'vue-toasted'

Vue.use(ToastedPlugin)

const {VLazyImagePlugin} = require('v-lazy-image')
Vue.use(VLazyImagePlugin)

const {Plugin} = require('vue-responsive-video-background-player')
Vue.use(Plugin)

import VueSocialChatPlugin from "vue-social-chat";

Vue.use(VueSocialChatPlugin)

import 'video.js/dist/video-js.css'

Vue.config.productionTip = false
Vue.config.errorHandler = error => {
  CSToast.error(`${error.name} ${error.message}`)
  CSToast.error(`${error.stack}`)
}

@Component({
  router,
  template:
    `
      <v-app :dark="model.isDark">
      <v-content>
        <transition name="zoom">
          <router-view/>
        </transition>
      </v-content>
      </v-app>
    `
})
export class Main extends BaseVue {
  mounted() {
    super.mounted()
    this.colors.updateTheme()
  }
}

const main = new Main()
later(() => main.$mount('#app'))
export default main


