





































































































































































import {Component} from 'vue-property-decorator'
import {BaseVue} from '@/View/BaseVue'
import MenuView from "@/View/Main/MenuView.vue";
import {VToolbar} from "@/cs/vuetify/CSVuetifyTypes";
import router from "@/Router";
import {Route} from "vue-router";
import Divider from "@/View/Pages/InstrumentsDivider.vue";

@Component({
  components: {Divider, MenuView}
})
export default class ToolbarView extends BaseVue {

  isActive = false
  isInstruments = false
  isSupport = false

  // cssVars() {
  //   return {
  //     '--toolbar-main-color': this.colors.title,
  //   }
  // }

  get toolbar() {
    return this.$refs.toolbar as VToolbar
  }

  mounted() {
    super.mounted();
    this.onRoute(router.currentRoute)
    router.beforeEach((to, from, next) => {
      this.onRoute(to)
      next()
    })
    window.addEventListener('scroll', this.onScroll);
    window.addEventListener("orientationchange", this.onOrientationChange);
    this.$router.afterEach(this.afterEachRoute)
  }

  onRoute(to: Route) {
    this.isInstruments = to.path.includes("instruments")
    this.isSupport = to.path.includes("support")
  }

  destroyed() {
    super.destroyed();
    window.removeEventListener('scroll', this.onScroll);
    window.removeEventListener("orientationchange", this.onOrientationChange);
  }

  isToolbarHidden = false
  previousScrollY = 0

  onScroll() {
    this.isToolbarHidden = window.scrollY > 0 && this.previousScrollY < window.scrollY
    if (window.scrollY > 0) this.previousScrollY = window.scrollY
    if (!this.isToolbarHidden) this.toolbar.isActive = true
  }

  onOrientationChange() {
    this.isToolbarHidden = false
    this.toolbar.isActive = true
    this.toolbar.isScrollingUp = true
  }

  afterEachRoute() {
    this.isToolbarHidden = false
    this.toolbar.isActive = true
    this.toolbar.isScrollingUp = true
  }
}
