import Vue from 'vue'
import Router from 'vue-router'
import MainView from '@/View/Main/MainView.vue'
import InstrumentsOverviewPage from '@/View/Pages/InstrumentsOverviewPage.vue'
import Routes from '@/Model/Routes'
import InstrumentsControllersPage from "@/View/Pages/InstrumentsControllersPage.vue";
import InstrumentsEffectsPage from "@/View/Pages/InstrumentsEffectsPage.vue";
import InstrumentsLooperPage from "@/View/Pages/InstrumentsLooperPage.vue";
import InstrumentsSupportPage from "@/View/Pages/InstrumentsSupportPage.vue";
import InstrumentsSoundsPage from "@/View/Pages/InstrumentsSoundsPage.vue";

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) return {selector: to.hash}
    else return savedPosition ? savedPosition : {x: 0, y: 0}
  },
  routes: [
    // {
    //   name: Routes.names.support,
    //   path: Routes.paths.support,
    //   beforeEnter() {
    //     location.href = 'https://support.renetik.com'
    //   }
    // },
    {
      path: '/',
      redirect: {path: '/instruments'}
    },
    {
      path: '/instruments',
      redirect: {path: '/instruments/overview'}
    },
    {
      path: '/instruments',
      component: MainView,
      children: [
        {
          name: Routes.names.support,
          path: Routes.names.support,
          alias: ['/instruments/support', '/support'],
          component: InstrumentsSupportPage,
        },
        {
          path: Routes.names.overview,
          name: Routes.names.overview,
          alias: ['/instruments/overview'],
          component: InstrumentsOverviewPage,
        },
        {
          path: Routes.names.controllers,
          name: Routes.names.controllers,
          alias: ['/instruments/controllers'],
          component: InstrumentsControllersPage,
        },
        {
          path: Routes.names.effects,
          name: Routes.names.effects,
          alias: ['/instruments/effects'],
          component: InstrumentsEffectsPage,
        },
        {
          path: Routes.names.sounds,
          name: Routes.names.sounds,
          alias: ['/instruments/sounds'],
          component: InstrumentsSoundsPage,
        },
        {
          path: Routes.names.looper,
          name: Routes.names.looper,
          alias: ['/instruments/looper'],
          component: InstrumentsLooperPage,
        },
      ]
    },
    {
      path: '*',
      redirect: {path: '/instruments/overview'}
    }
  ]
})

export default router
