













import {Component, Prop} from 'vue-property-decorator'
import {BaseVue} from '@/View/BaseVue'
import videojs from 'video.js'

@Component
export default class extends BaseVue {
  @Prop({required: true}) src!: string
  player: videojs.Player | null = null
  isVisible = false

  options: videojs.PlayerOptions = {
    autoplay: true,
    bigPlayButton: false,
    controlBar: {fullscreenToggle: false},
    controls: true,
    sources: [{src: this.project.products.keys.headerVideo.url}]
  }

  show() {
    if (!this.player) this.player = videojs(this.$refs.videoPlayer, this.options)
    else {
      this.player.currentTime(0)
      this.player.play()
    }
    this.isVisible = true
  }

  hide() {
    this.isVisible = false
    this.player?.pause()
  }

  destroyed() {
    super.destroyed()
    this.player?.dispose()
  }
}
