    "&download=true" +
    "&show_artwork=true" +
    "&show_playcount=false" +
    "&show_user=false" +
    "&start_track=0" +
    "&single_active=true" +
    "&hide_related=true" +
    "&show_comments=false" +
    "&show_user=false&" +
    "&show_reposts=false" +
    "&show_teaser=false" +
    "&visual=false"
}
