import colors from '@/View/Colors'
import {CSBooleanProperty} from '@/cs/CSProperty'
import device from '@/cs/CSDevice'

export class Model {
  isDarkMode = new CSBooleanProperty(device.isDarkMode, () => { colors.updateTheme() })

  // get isDark() { return false }
  get isDark() { return this.isDarkMode.value }

  constructor() {
    device.onDarkModeChange(() => { this.isDarkMode.value = device.isDarkMode })
  }

  get isProduction() { return process.env.NODE_ENV === 'production' }
}

export default new Model()

