import '@/cs/extensions/CSLang'
import Vue from 'vue'
import {event} from '@/cs/event/CSEvent'

export class CSVue extends Vue {

  eventDestroyed = event()

  mounted() {
    // console.log(`mounted ${this.constructor}`)
  }

  updated() {
    // console.log(`updated ${this.constructor}`)
  }

  destroyed() {
    // console.log(`destroyed ${this.constructor}`)
    this.eventDestroyed.fire()
  }

  updatedOnWindowEvent(type: string, method: () => void) {
    method()
    window.addEventListener(type, method)
    this.eventDestroyed.listen(() => {
      window.removeEventListener(type, method)
    })
  }

  updatedOnWindowResize(method: () => void) {
    this.updatedOnWindowEvent('resize', method)
  }

  get mdAndUp() {
    return this.$vuetify.breakpoint.mdAndUp
  }

  get lgAndDown() {
    return this.$vuetify.breakpoint.lgAndDown
  }

  get mdAndDown() {
    return this.$vuetify.breakpoint.mdAndDown
  }

  get sm() {
    return this.$vuetify.breakpoint.sm
  }

  get smAndDown() {
    return this.$vuetify.breakpoint.smAndDown
  }

  get xs() {
    return this.$vuetify.breakpoint.xs
  }

  get xlAndDown() {
    return this.$vuetify.breakpoint.xlOnly
  }

  get lgAndUp() {
    return this.$vuetify.breakpoint.lgAndUp
  }

  goTo(target: string) {
    // noinspection JSIgnoredPromiseFromCall
    this.$vuetify.goTo(target)
  }
}


